.searchPage__logo {
  object-fit: contain;
  height: 50px;
  margin-right: 50px;
}

.searchPage__header {
  display: flex;
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 100;
  align-items: flex-start;
  padding: 30px;
  border-bottom: 1px solid lightgray;
}

.searchPage__headerBody > .search > .search__input {
  margin-top: 0;
  width: unset;
  margin: unset;
  max-width: unset;
}

.searchPage__options {
  display: flex;
  align-items: center;
  color: grey;
  margin-top: 15px;
  margin-left: 5px;
}

.searchPage__optionsLeft,
.searchPage__optionsRight {
  display: flex;
}

.searchPage__options a {
  text-decoration: none;
  color: gray;
}

.searchPage__optionsRight {
  margin-left: 80px;
}

.searchPage__option {
  display: flex;
  align-items: center;
  margin-right: 20px;
}

.searchPage__option a {
  margin-left: 5px;
  font-size: 13px;
}

.searchPage__option .MuiSvgIcon-root {
  font-size: 20px;
}

.searchPage__resultCount {
  color: #70757a;
  font-size: 14px;
  margin-bottom: 24px;
}

.searchPage__result {
  margin-top: 0;
  margin-bottom: 24px;
}

.searchPage__results {
  max-width: 650px;
  margin-top: 20px;
  margin-left: 240px;
  margin-bottom: 100px;
}

.searchPage__resultTitle {
  text-decoration: none;
}

.searchPage__resultTitle:hover {
  text-decoration: underline;
}

.searchPage__resultSubTitle {
  text-decoration: none;
  color: #202124;
  font-size: 14px;
  padding-top: 1px;
  line-height: 1.3;
  display: flex;
  align-items: center;
}

.searchPage__resultSubTitle:hover {
  text-decoration: underline;
}

.searchPage__resultTitle > h2 {
  font-weight: 500;
  color: #1a0dab;
  font-size: 20px;
  line-height: 1.3;
}

.searchPage__resultSnippet {
  margin-top: 3px;
  color: #4d5156;
  line-height: 1.58;
  font-size: 14px;
}

.searchPage__resultImage {
  object-fit: contain;
  height: 20px;
  width: 20px;
  margin-right: 10px;
}
